import React from 'react';
import { Link } from 'react-router-dom';
import api from '../util/api.js';
import Search from './Search.js';

class Nav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      location: '',
    }
  }
  
  handleSubmit = (event, location) => {
    event.preventDefault();

    let value = window.encodeURI(location);
    let redirectURI = '/weather-app/forecast?city=' + value;
    
    this.props.history.push(redirectURI);
  }

  render() {
    return (
      <div className='nav' >
        <Link className='link' to='/weather-app/' ><h1>Best Weather App</h1></Link>
        <Search formType='nav-form' onSubmit={this.handleSubmit} />
      </div>
    )
  }
}

export default Nav;